



document.addEventListener('DOMContentLoaded',()=>{
  
    cargarSpinning();
    iniciarApp();
    
})

function cargarSpinning(){

    setTimeout(()=>{
     
   
        const containerLoader = document.querySelector('.container-loader');
        containerLoader.classList.add('loader-hidden');
       
       
    },1000)

}


function iniciarApp(){

  
    const botonGeneros = document.querySelector('.ul-peliculas');//SELECCIONO EL BOTON DE GENEROS

    if(botonGeneros != null){
    botonGeneros.addEventListener('click',desplegarPeliculas); //QUE DESPLEGUE LAS PELICUOAS
  }
  
    const botonMenu = document.querySelector('.boton-menu');
  
    if(botonMenu != null){
    
    botonMenu.addEventListener('click',desplegarMenu);
  }
  

     

   
     // //QUE EL HEADER PASE A SER FIXED
     fijarHeader();
  
   
  
    // //FLECHA PARA SUBIR AL PRINCIPIO DE LA PAGINA
     const flechaSubir = document.querySelector('.flecha');
  
     if(flechaSubir != null){
  
      subirTop(flechaSubir);
  
     }
  
  
     generarBoton();
  
     ocultarLogin();
    
    
     botonVerMas();

    ocultarErrores();


    enviarMail();



}

function enviarMail(){
    const btnEnvio = document.getElementById('enviar-correo');
    if(btnEnvio!=null){
        btnEnvio.addEventListener('click',(e)=>{
            e.preventDefault();
      
            const email = document.querySelector('.form-email').value;
            const nombre = document.querySelector('.form-nombre').value;
            const apellido = document.querySelector('.form-apellido').value;
            const mensaje = document.querySelector('#form-descripcion').value;

            window.location.href = `mailto:hernandezgonzalo5845@gmail.com?subject=Contactoparapropuesta&body=Nombre%3A${nombre}%0AApellido%3A%20${apellido}%0AMail%3A${email}%0AMensaje%3A${mensaje}`;

           redireccionarIndex();
        });
    }

}

function redireccionarIndex(){
    window.location.href="/";
}


function ocultarErrores(){
    const errores = document.querySelectorAll('.container-errores p');
    errores.forEach(error => {
    
        setTimeout(() => {
            error.style.display = 'none';
        }, 7000);
        
    });
}

function botonVerMas(){
 
    const containers = document.querySelectorAll(".container-descripcion");
   


    containers.forEach((container) => {
      
        if(container!=null){
            const moreBtn = document.querySelector(".btn-mas");
            console.log(moreBtn);
            const text = container.querySelector(".container-descripcion p");

            desplegarDescripcion(moreBtn,text);
        }
     
    })
    }





function desplegarDescripcion(moreBtn,text){
    moreBtn.addEventListener("click", () => {
        if (text.style.height == "auto") {
            text.style.height = "130px";
            moreBtn.innerHTML = "Ver mas";
          
          
        } else {
            text.style.height = "auto";
            moreBtn.innerHTML = "Ver menos";
        }
      });
    }

    

function ocultarLogin(){
    const btnMenu = document.querySelector('.boton-menu');
   
    
    btnMenu.addEventListener('click',()=>{
        const formLog = document.querySelector('.container-formlog');
        if(formLog!=null){
            if(formLog.classList.contains('novisible')){
                formLog.classList.remove('novisible')
            }else{
                formLog.classList.add('novisible')
            }
    }
    })

}

function generarBoton(){
    const inputSearch = document.querySelector('.search_movie');
    const btnBuscar = document.querySelector('.btnBuscar');
    if(inputSearch!=null){
    inputSearch.addEventListener('keyup',()=>{
      
        if( inputSearch.value!= '' ){
         
          
            btnBuscar.classList.add('visible');
      
            // buscarPeliculas(inputSearch.value); 
         
    
        }else{
            btnBuscar.classList.remove('visible');
        }
    })
}
  
}

function buscarPeliculas(pelicula){
    $.ajax({
        url: "miArchivo.php",
        type: "POST",
        data: { pelicula: pelicula },
        success: function(response) {
            console
        }
      });

}

