function subirTop(flechaSubir){
    if(flechaSubir!=null){
    flechaSubir.addEventListener('click',()=>{
        window.scrollTo({ top: 0, behavior: 'smooth' }); //ME LLEVA AL INICIO DE LA PAGINA
    })
}
}

function fijarHeader(){
    const contNav = document.querySelector('.container-nav');
    const body = document.querySelector('body')
   window.addEventListener('scroll',()=>{
    const positionTop = body.getBoundingClientRect().top;
    if(positionTop<(-215)){
   
  
        contNav.classList.add('cambiarFixed');
        body.classList.add('scroll-fijo');

    }else{
        contNav.classList.remove('cambiarFixed');
        body.classList.remove('scroll-fijo');

    }
   })
 
    
}


function navegacionSmooth(){

    const enlaces = document.querySelectorAll('.smooth');

    
    enlaces.forEach(enlace => {
    
        enlace.addEventListener('click',(e)=>{

            e.preventDefault();
            const href = e.target.attributes.href.value;
       
            const seccion = document.querySelector(href);
    
            seccion.scrollIntoView({
                block: "center",
                behavior: 'smooth'});
        })

        
    });
    
}
    


function desplegarMenu(){

    const contNav = document.querySelector('.container-nav');
 
   contNav.classList.toggle('contVisible');
        

}


function desplegarPeliculas(){
    const contPeliculas = document.querySelector('.contenedor-peliculas');
    

        if(contPeliculas.classList.contains('contVisible')){ //SIN CONTPELICULAS TIENE LA CLASE "contVisible"
            contPeliculas.classList.remove('contVisible'); //QUE SAQUE LA CLASE Y QUE NO SE MUESTRE EL LSITADO DE PELICULAS
           
      
        }else{
            contPeliculas.classList.add('contVisible'); //QUE SE MUESTRE EL LISTADO DE
        }
  
        girarFlecha();
        controlarPosition();
 

  
}


function controlarPosition(){
    const contPeliculas = document.querySelector('.contenedor-peliculas');
    
    if(contPeliculas.classList.contains('cambiarRelative')){
        contPeliculas.classList.remove('cambiarRelative');
       }else{
        contPeliculas.classList.add('cambiarRelative');
       }
   
}

function girarFlecha(){
    const flecha = document.querySelector('.arrow');
    if(flecha.classList.contains('rotar45')){
        flecha.classList.remove('rotar45');
    }else{
        flecha.classList.add('rotar45');
    }

}

function controlarResolucion(){

    return window.screen.width;
}



